import React, { FC } from 'react';
import styles from './haracterPreview.module.scss';

interface haracterPreviewProps {
  image: string
  name: string
}

const haracterPreview: FC<haracterPreviewProps> = ({ image, name }) => {
  return (
    <li className={styles['character']}>
      <span className={styles['character__title']}>{name}</span>
      <div className={styles['character__img-wrapper']}>
        <img src={image} alt="character" />
      </div>
    </li>
  );
};

export default haracterPreview;
